@import "../../../GlobalStyles/global.scss";

.input {
  margin: 0;
}
.inputCotainer {
  display: flex;
  width: 421px;
  margin-top: 65px;
  margin-left: 69px;
  justify-content: space-between;
}

.input-otp {
  width: 70px;
  height: 91px;
  text-align: center;
  font-size: $fs-28;
  color: $color-green;
  border: 1px solid $color-green;
  border-radius: 10px;
}


.no-underline {
  text-decoration: none;
}

.background {
  width: 509px;
  height: 716px;
}

.rightBackground {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btnBackLogin {
  margin-top: 12px;
}

.btnSubmit {
  margin-top: 118px;
}

.note {
  width: 489px;
  height: 18px;
  font-family: $ff-popins;
  color: $color-content-box;
  font-size: $fs-12;
  font-weight: $fw-400;
  margin-top: 1px;
  margin-bottom: 0px;
}

.title {
  width: 621px;
  height: 41px;
  font-family: $ff-montserrat;
  color: $color-black;
  font-size: $fs-34;
  font-weight: $fw-700;
  margin-top: 77px;
  margin-bottom: 0px;
}

.textLogo {
  color: $color-black;
  font-size: $fs-52;
  font-weight: $fw-700;
  line-height: 40px;
  margin-left: 23px;
  font-family: $ff-montserrat;
}

.logo {
  display: flex;
  width: 100%;
  height: 98px;
  align-items: center;
  padding-top: 46px;
}

.leftBackground {
  width: 52%;
  height: 100vh;
  padding-left: 68px;
}

.container {
  height: 100vh;
  width: 100%;
  display: flex;
}
